













import Vue from 'vue'
import moment from 'moment'



export default Vue.extend({
  name: 'DataItem',
  props: [ 'event', 'time', 'type' ],
  filters: {
    truncate: function (text : string, stop : number, clamp : string ) {
    return text.slice(0, stop) + (stop < text.length ? clamp || '...' : '');
    }
  }
});
